.words-container{
    padding-top: 1rem;
}

/* Fix table head */
.tableFixHead    { overflow: auto; height: 250px; }
.tableFixHead th { position: sticky; top: 0; }

/* Just common table stuff. */
table  { border-collapse: collapse; width: 100%; }
th, td { padding: 8px 16px; }
th     { background:#eee; }



.table th, .table td {
    padding: 0.01rem;
    vertical-align: top;
    border-top: 1px solid #dee2e6;
}


.word-form{
    text-align: center;
}

.type-radio{
    display: inline-block;
    margin: 0 1rem;
}

.type-radio input{
    height: 15px;
    width: 15px;
}


.word-form input{
    width: 90%;
}

.note{
    margin: 2px 0;
}


.search{
    border-radius: 5px;
    margin-bottom: 0.5rem;
    width: 50%;
}